@import url(https://fonts.googleapis.com/css?family=Roboto:400,100,900);

@font-face {
  font-family: "Akkurat-Mono";
  src: url("./Akkurat-Mono.ttf") format("truetype");
  /* Modern Browsers */
  font-weight: normal;
  font-style: normal;
}

.body {
  background-color: #000000;
}

.main-btn {
  box-sizing: inherit;
  transition-property: all;
  transition-duration: .6s;
  transition-timing-function: ease;

  color: #fff;
  background-color: transparent;
  border: 1px solid transparent;
  font-size: 48px;
  font-weight: 400;
  line-height: 45px;
  margin: 1em 0 0 0;
  position: relative;
  text-decoration: none;
  text-transform: uppercase;
}

.main-btn:hover, .main-btn:active {
  letter-spacing: 7px;
}

.main-btn:after, .main-btn:before {
  backface-visibility: hidden;
  border: 2px solid rgba(0.5, 0.7, 1, 0);
  bottom: 0;
  content: " ";
  display: block;
  margin: 0 auto;
  position: relative;
  transition: all 280ms ease-in-out;
  width: 0;
}

.main-btn:hover:after, .main-btn:hover:before {
  backface-visibility: hidden;
  border-color: #a95cfa;
  transition: width 350ms ease-in-out;
  width: 100%;
}

.main-btn:hover:before {
  bottom: auto;
  top: 0;
  width: 100%;
}

.main-img {
  z-index: -50;
  height: 100vh;
  width: 100%;
  object-fit: cover;
}

.ripped {
  background: black;
  width: 0;
  height: 100vh;
  position: absolute;
  z-index: 10;
  left: 58%;
  top: 0;
}

.ripped::after {
  content: "";
  position: absolute;
  height: 100%;
  width: 50px;
  top: 0;
  left: 100%;
  background: url(rip.svg) right;
  background-size: 300%;
  z-index: -10;
}

.has-dropcap:first-letter {
  font-family: "Akkurat-Mono", Arial, Helvetica, sans-serif;
  float: left;
  font-size: 6rem;
  line-height: 0.65;
  margin: 0.1em 0.1em 0.2em 0;
}

.drop-cap {
  font-family: "Akkurat-Mono", Arial, Helvetica, sans-serif;
  color: #303030;
  float: left;
  font-size: 6rem;
  line-height: 1;
  margin: 0.1em 0.1em 0.2em 0;
  padding: 0.1em;
}
.drop-cap:before,
.drop-cap:after {
  content: "";
  display: block;
}
.drop-cap:before {
  margin-top: -0.2em;
}
.drop-cap:after {
  margin-bottom: -0.15em;
}

p {
  font-family: "Akkurat-Mono", Arial, Helvetica, sans-serif;
  font-size: 1.5em;
}

.bold {
  font-weight: 900;
}

.title-h1 {
  font-family: Akkurat-Mono, sans-serif;
  font-size: 3.5em;
  text-align: center;
}

.title-h2 {
  font-family: Akkurat-Mono, sans-serif;
  font-size: 3em;
  text-align: center;
}

.title-h3 {
  font-family: Akkurat-Mono, sans-serif;
  font-size: 2em;
  text-align: center;
}

.title-h4 {
  font-family: Akkurat-Mono, sans-serif;
  font-size: 2em;
  text-align: start;
}

.caption {
  font-family: Akkurat-Mono, sans-serif;
  font-size: 1em;
  text-align: start;
}

.footnote {
  vertical-align: super;
  font-size: 0.75em;
}